platform-bar {
  display: block;
  height: 44px;
  background: var(--c-carbon-100);
}
platform-bar .wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-family: var(--f-family-text);
  padding-block-start: 0;
  padding-block-end: 0;
}
platform-bar .nowrap {
  display: inline-block;
  white-space: nowrap;
}
platform-bar .platform-bar__logo {
  margin-inline-end: var(--space-3);
}
platform-bar .platform-bar__logo svg {
  height: 24px;
  margin: 10px 0;
  vertical-align: middle;
}
platform-bar .platform-bar__desc {
  align-content: flex-end;
  color: #ccc;
  font-size: var(--f-size-2);
  line-height: 160%;
  text-align: right;
  letter-spacing: var(--f-tracking-tight);
  margin: 0 -3px 0 0;
}
platform-bar .platform-bar__desc strong {
  color: white;
  font-weight: 400;
}
platform-bar .platform-bar__desc a {
  color: white;
  text-decoration: none;
  white-space: nowrap;
}
platform-bar .platform-bar__desc ion-icon {
  display: flex;
  align-items: center;
  margin-inline-start: var(--space-0);
}